* {
  font-family: 'Roboto', serif; }

nav {
  position: fixed;
  width: 100%;
  height: 51px;
  background-color: #212121;
  z-index: 2; }

header {
  height: 50px;
  position: relative; }

.menu {
  display: flex;
  justify-content: space-between;
  padding: 13px 70px;
  margin: 0;
  list-style-type: none; }

.menu_link {
  font-family: Roboto;
  font-size: 15px;
  font-weight: 300;
  color: #ffffff; }

.menu_item {
  border-right: 2px dotted black;
  padding-right: 28px; }

.subheader {
  position: absolute;
  top: 50px;
  width: 100%;
  height: 84px;
  background-color: rgba(0, 0, 0, .6); }

.subheader_logo {
  display: block;
  max-width: 100%;
  margin-top: 16px; }

.subheader_official {
  margin-top: 6px;
  opacity: 0.5;
  font-size: 13px;
  font-weight: 100;
  color: #ffffff; }

.subheader_call {
  text-align: right;
  margin-top: 19px;
  font-size: 12px;
  font-weight: 300;
  color: #ffffff; }

.subheader_phone {
  margin-top: 7px;
  display: block;
  text-align: right;
  font-family: Roboto;
  font-size: 22px;
  font-weight: 400;
  text-transform: uppercase;
  color: #ffffff;
  line-height: 12px; }

.subheader_btn {
  margin-top: 16px;
  width: 178px;
  height: 52px;
  background-image: linear-gradient(0.0deg, #1eacc7 0%, #4ce2ff 100%);
  border: none;
  border-radius: 4px;
  box-shadow: inset 0px -3px 0px 0px rgba(0, 0, 0, 0.11);
  font-family: Roboto;
  font-size: 14px;
  font-weight: 400;
  text-transform: uppercase;
  text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.2);
  color: #ffffff; }

.promo {
  min-height: 800px;
  background: url("../img/bg/bg.png") center center/cover no-repeat;
  padding: 150px 0 135px 0; }

.promo_header {
  font-family: Roboto;
  font-size: 60px;
  text-transform: uppercase;
  color: #ffffff;
  font-weight: 700;
  line-height: 60px;
  margin: 0;
  text-align: center; }

.promo_subheader {
  font-family: Roboto;
  font-size: 36px;
  text-transform: uppercase;
  color: #ffffff;
  font-weight: 700;
  line-height: 36px;
  margin: 0px;
  margin-top: 22px;
  text-align: center; }

.promo_descr {
  margin-top: 23px;
  font-size: 18px;
  line-height: 24px;
  font-weight: 400;
  color: #ffffff;
  text-align: center; }

.promo_button {
  display: block;
  margin: 115px auto 0 auto;
  width: 265px;
  height: 69px;
  background-image: linear-gradient(0.0deg, #1eacc7 0%, #4ce2ff 100%);
  border: none;
  border-radius: 4px;
  box-shadow: inset 0px -4px 0px 0px rgba(0, 0, 0, 0.11);
  font-family: Roboto;
  font-size: 18px;
  line-height: 68px;
  font-weight: 400;
  text-transform: uppercase;
  text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.2);
  color: #ffffff; }

.icons {
  margin-bottom: 65px; }

.icons_block {
  margin-top: 40px;
  display: flex;
  justify-content: center;
  align-items: center; }

h2 {
  &.icons {
    text-align: center;
    margin-top: 133px;
    font-size: 38px;
    font-weight: 700;
    text-transform: uppercase;
    color: #222222; } }

.icons_descr {
  width: 343px; }

.icons_round {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 26px;
  width: 113px;
  height: 113px;
  background-color: #1eacc7;
  border-radius: 100%; }

.icons_text1 {
  font-size: 17px;
  font-weight: 400;
  color: #1eacc7; }

.icons_text2 {
  font-size: 15px;
  font-weight: 300;
  color: #202020; }

.mobile {
  height: 863px;
  background: url("../img/bg/bg.png");

  &title {}

  font-size: 38px;
  font-weight: 700;
  text-transform: uppercase;
  color: #222222;
  text-align: center;
  padding: 110px 0 80px 0; }




